<template>
<div class="movie-view has-header">
  <scroller title="" type="hasCover" :items="hotMovies"></scroller>
  <scroller title="" type="hasCover" :items="topMovies"></scroller>
  <scroller title="" type="hasCover" :items="newMovies"></scroller>
  <scroller title="" type="onlyString" :items="movieTags"></scroller>
  <types></types>
  <download-app></download-app>
</div>
</template>

<script>
import {
  mapState
} from 'vuex'

import Scroller from '../components/Scroller'
import Types from '../components/Types'
import DownloadApp from '../components/DownloadApp'

export default {
  name: 'movie-view',
  components: {
    Scroller,
    Types,
    DownloadApp
  },
  data () {
    return {}
  },
  computed: {
    // 
    ...mapState({
      hotMovies: state => state.movie.hotMovies,
      topMovies: state => state.movie.topMovies,
      newMovies: state => state.movie.newMovies,
      movieTags: state => state.movie.movieTags
    })
  },
  methods: {
    // 
    getMovie: function () {
      this.$store.dispatch('getMovie')
    }
  },
  created () {
    this.getMovie()
  }
}
</script>

<style scoped>

</style>



